table {
	width: 100%;
	// font-size: 0.8em;

	@include screen-size(medium-large) {
		border-top: 1px solid $colour-accent;
		background: white;

		&.framed {
			padding: 30px;
			background: white;
			border-radius: $border-radii;
			box-shadow: $default-box-shadow;
			border: none;
		}

		&.hd {
			font-size: 0.8em;

			th,
			td {
				border-bottom: 1px solid $colour-accent;
				padding: 0.3em 0.5em;
				line-height: 1.3;
			}
		}

		td.actions {
			text-align: right;

			form {
				display: inline;
			}

			a,
			button {
				color: #666;
			}
		}

		th,
		td {
			border-bottom: 1px solid $colour-accent;
			padding: 0.2em 0.8em;
			line-height: 1.5;
		}
	}

	th,
	td {
		@include screen-size(small) {
			display: block;
			padding-left: 0;
			padding-right: 0;
		}
	}

	thead {
		@include screen-size(small) {
			display: none;
		}

		th,
		td {
			vertical-align: bottom;
			position: -webkit-sticky;
			position: sticky;
			top: -31px;
			background: white;
			z-index: 1;

			// @include screen-size(medium-large) {
			// 	border-top: 1px solid $colour-accent;
			// }
		}
	}

	tbody {
		tr {
			@include screen-size(small) {
				display: grid;
				grid-template-areas: "main functions";
				justify-content: space-between;
				grid-area: main;
				background: white;
				padding: 15px;
				border-radius: $border-radii;
				margin-bottom: 20px;
				box-shadow: $default-box-shadow;
			}

			td,
			th {
				vertical-align: top;

				@include screen-size(small) {
					margin-bottom: 10px;
					padding: 0;

					&:last-child {
						margin-bottom: 0;
					}

					&:before {
						content: attr(data-title);
						font-weight: bold;
						color: black;
						display: block;
						font-size: 0.8em;
					}
				}

				[class^="icon-"]:not(.iconified), 
				[class*=" icon-"]:not(.iconified) {
					display: inline-block;
					position: relative;
					padding-left: 26px;

					&:before {
						position: absolute;
						left: 0;
						top: 50%;
						transform: translateY(-50%);;
					}
				}

				&.functions {
					text-align: right;

					@include screen-size(medium-large) {
						padding-left: 20px;
						width: 1px;
						white-space: nowrap;
						width: 0;
					}

					@include screen-size(small) {
						grid-area: functions;
					}

					.icon {
						display: inline-block;
						vertical-align: middle;
						width: 22px;
						height: 22px;
						line-height: 22px;

						@include screen-size(medium-large) {
							margin-left: 20px;
						}

						@include screen-size(small) {
							margin-bottom: 20px;
						}
					}
				}
			}

			&.archived {
				td {
					&.name a {
						color: gray;

						&:after {
							font-family: fontello;
							content: '#{$glyph-archived}';
							font-size: 0.6em;
							vertical-align: middle;
							padding-left: 0.5em;
							position: relative;
							top: -0.05em;
						}
					}
				}
			}
		}

		// tr:first-child {
		// 	th {
		// 		padding-top: 1.5em;
		// 		font-style: italic;
		// 	}
		// }
	}


	&.diagonal-headers {		

		thead {

			th {
				height: 120px;
				position: relative;
				background: transparent;
				top: 0;

				.label {
					-webkit-transform: rotate(-45deg);
					transform: rotate(-45deg);
					transform-origin: 0 0;
					white-space: nowrap;
					overflow: hidden;
					text-overflow: ellipsis;
					position: absolute;
					bottom: 0;
					width: 160px;
					// background: white;
					cursor: default;
					transition: width 1s;
				}
			}

			&:hover {
				th {
					.label {
						width: auto;
						background: white;
					}
				}
			}
		}

	}
}