$portal-header-shade: light;

// Primary colours

$colour-obsidian: #000000;
$colour-anthracite: #444444;
$colour-selenit: #707070;
$colour-concrete: #9E9E9E;
$colour-siver-arrow: #C8C8C8;
$colour-pebble: #D9D9D9;
$colour-mercury: #E6E6E6;
$colour-cloud: #F2F2F2;

// Support colours

$colour-deep: #003340;
$colour-goose: #004355;
$colour-nordic: #00566a;
$colour-calypso: #00677f;
$colour-racing: #007a93;
$colour-azure: #5097ab;
$colour-glacier: #79aebf;
$colour-sky: #a6cad8;
$colour-cloud: #F2F2F2;

// Status colours

$colour-pine: #aac891;
$colour-signal-green: #427a15;
$colour-squash: #f0be7d;
$signal-orange: #e69123;
$colour-hyazinth: #9f1924;
$colour-signal-red: #d60000;

// Visualising information

$colour-sand: #D3B264;
$colour-pistachio: #826C39;

// OiRA parameters

$body-font-family: 'DaimlerCS-Regular', sans-serif;
$colour-primary: $colour-racing; 
$colour-base: $colour-mercury; 
$colour-menu-lines: #c8c8c8; 
$colour-error: $colour-signal-red; 
$colour-accent: $colour-pistachio; 
$colour-toolbar: $colour-cloud;
$colour-toolbar-elements: $colour-anthracite;
$colour-link: $colour-racing;
$colour-tag: $colour-sky;

$arrow-down-white: "";

$colour-lines: $colour-mercury;
$colour-separation-lines: silver;

$button-text-colour: black;
$button-background-colour: white;
$button-background-colour-hover: $colour-cloud;
$button-text-colour-hover: black;

$button-height: 49px;
$button-line-height: 52px;
$button-small-height: 29px;
$button-small-line-height: 30px;

$button-shadow: inset 0 0 0 3px black;
$button-shadow-default: none;
$button-small-shadow: inset 0 0 0 1px black;

$button-background-colour-default: black;
$button-background-colour-default-hover: $colour-selenit;

$button-background-colour-disabled: silver;
$button-background-colour-disabled-hover: silver;
$button-text-colour-disabled: gray;
$button-text-colour-disabled-hover: gray;


$back-button-border-radius-left: 0;

$nav-bar-button-height-mobile: 35px;
$nav-bar-button-line-height-mobile: 36px;

$nav-bar-button-height: 49px;
$nav-bar-button-line-height: 52px;

$colour-toolbar-button: $button-background-colour-default;

$colour-info: $colour-pine;
$colour-warning: $signal-orange;
$colour-success: $colour-signal-green;

$global-header-box-shadow: black;
$global-header-box-shadow-intensity: 0.08;
$global-header-box-shadow-size: 9px;

$border-radii: 0;

