	.context-menu {
		.context-menu-label.iconified.pat-button {

			&:after {
				background-image: url(data:image/svg+xml;base64,#{$arrow-down-white});
			}

			&.collapsible-open {
				background-color: $colour-accent;

				&:before {
					color: white;
				}
			}
		}
	}

	.pat-rich {
		.summary {
			font-weight: thin
		}

		h1 {
			font-size: 48px;
			line-height: 52px;
			font-weight: 400;

			@media only screen and (max-width: 768px) {
				font-size: 28px;
				line-height: 36px;
			}
		}

		h2 {
			margin-bottom: 13px;
			font-size: 24px;
			line-height: 30px;
			// font-family: DaimlerCS-Demi;
			padding-top: 52px
		}

		em,
		i {
			font-style: italic;
			color: black;
		}

		strong {
			font-weight: bold;
		}

		ul,
		ol,
		table,
		p,
		dl {
			margin-bottom: 1.15em;
		}
	}

	form {
		fieldset.vertical {
			:not(.pat-checklist) legend,
			:not(.pat-checklist) .legend {
				margin-bottom: 13px;
				font-size: 24px;
				line-height: 30px;
				// font-family: DaimlerCS-Demi;
