.pat-toolbar {
	&#training-toolbar {
		.toolbar-navigation-area,
		.toolbar-functions-area {
			@include screen-size(medium-large) {
				grid-template-columns: 3fr 0fr 3fr;
			}
		}
	}
	&#assessment-toolbar {
		.toolbar-navigation-area,
		.toolbar-functions-area {
			@include screen-size(medium-large) {
				grid-template-columns: 2fr 0fr 2fr;
			}
		}
	}
}